.products__card {
    background-color: #F9F8F8;
    border: none;
    border-radius: 10px;
    display: flex;
}
.products__img {
    margin: 0 auto;
    width: 200px;
    padding: 12px;
}

.about_product {
    text-align: center;
    padding-top: 24px;
    padding-right: 12px;
}
.products__name {
    margin-top: 18px;
    margin-bottom: 12px;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1, 6;
}

.products__price {
    margin-bottom: 12px;
    font-family: 'Open Sans';
    font-weight: 600;
    font-size: 16px;
    line-height: 1, 8;
}

.number {
    border-radius: 10px;
    height: 18px;
    padding-left: 36px;
}
.button {
    height: 24px;
    width: 24px;
    border: none;
    background-color: inherit;
}
.button:hover {
color: #2196f3;
}
.count {
    display: flex;
    column-gap: 4px;
    align-items: center;
    margin-left: 16px;
}

.count__input {
    max-width: 100%;
    padding-top: 4px;
    background-color: inherit;

    text-align: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.3;
}

.count__controls {
    display: flex;
    flex-direction: column;
}

.count__input::-webkit-inner-spin-button,
.count__input::-webkit-outer-spin-button {
    appearance: none;
}